@charset "utf-8";

@import "@fortawesome/fontawesome-free/css/all.min.css";
@import "fixed-data-table-2/dist/fixed-data-table.min.css";

$black: #4a4a4a !default;

$gray: #bebebe !default;
$lightGray: #f5f5f5 !default;
$lighterGray: #f7f7f7 !default;
$darkGray: #b4b4b4 !default;

$blue: rgb(37, 100, 225) !default;
$lightBlue: rgb(190, 210, 255) !default;
$lighterBlue: #f4feff !default;
$darkBlue: rgb(33, 83, 185) !default;

$green: rgb(19, 217, 46) !default;
$lightGreen: rgb(198, 255, 190) !default;
$darkGreen: rgb(16, 191, 39) !default;

$red: rgb(198, 64, 64) !default;
$lightRed: rgb(252, 178, 178) !default;
$darkRed: rgb(172, 31, 31) !default;

$orange: rgb(230, 180, 28) !default;
$lightOrange: rgb(255, 228, 132) !default;
$darkOrange: rgb(230, 163, 28) !default;

$backgroundLight: $lightGray !default;
$activeItem: $blue !default;

// reset

/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

html {
    overflow: hidden;
}

body {
    overflow: hidden;
}

// basic customizations

b {
    font-weight: bold;
}

h1, h2, h3, h4, h5 {
    font-weight: bold;
}

h1 {
    font-size: 2em;
}

h2 {
    font-size: 1.75em;
}

h3 {
    font-size: 1.5em;
}

h4 {
    font-size: 1.25em;
}

h5 {
    font-size: 1em;
}

dl dt {
    font-family: inherit;
    font-weight: bold;
    padding-bottom: 2px;
}

dl dd {
    font-family: inherit;
    text-indent: 1em;
    padding-bottom: 5px;
}

// helpers

.is-white {
    color: white;
}
.is-background-white {
    background-color: white;
    border-color: $gray;
}

.is-black {
    color: $black;
}
.is-background-black {
    background-color: $black;
    border-color: white;
}

.is-gray {
    color: $gray;
}
.is-background-gray {
    background-color: $gray;
    border-color: $lightGray;
}
.is-gray.is-light {
    color: $lightGray;
}
.is-background-gray.is-light {
    background-color: $lightGray;
    border-color: $gray;
}

.is-blue {
    color: $blue;
}
.is-background-blue {
    background-color: $blue;
    border-color: $lightBlue;
}
.is-blue.is-light {
    color: $lightBlue;
}
.is-background-blue.is-light {
    background-color: $lightBlue;
    border-color: $blue;
}

.is-green {
    color: $green;
}
.is-background-green {
    background-color: $green;
    border-color: $lightGreen;
}
.is-green.is-light {
    color: $lightGreen;
}
.is-background-green.is-light {
    background-color: $lightGreen;
    border-color: $green;
}

.is-red {
    color: $red;
}
.is-background-red {
    background-color: $red;
    border-color: $lightRed
}
.is-red.is-light {
    color: $lightRed;
}
.is-background-red.is-light {
    background-color: $lightRed;
    border-color: $red;
}

.is-orange {
    color: $orange;
}
.is-background-orange {
    background-color: $orange;
    border-color: $orange;
}
.is-orange.is-light {
    color: $lightOrange;
}
.is-background-orange.is-light {
    background-color: $lightOrange;
    border-color: $orange;
}

// form

fieldset {
    border-radius: 6px;
    display: block;
    padding: 0 10px 5px 10px;
    border: 1px solid #ddd;
    margin: 0 5px 0 5px;
}

fieldset>legend {
    display: block;
    font-size: 0.8rem;
    padding: 0 5px;
    width: max-content;
}

.diamond-button {
    cursor: pointer;
    padding: 7px;
    border-style: solid;
    border-width: 1px;
    text-align: center;
    border-radius: 5px;
    user-select: none;
    color: white;
}

a.diamond-button:visited {
    cursor: pointer;
    text-decoration: inherit;
}

.diamond-button.is-background-white:hover {
    background-color: $lightGray;
}
.diamond-button.is-background-gray:hover {
    background-color: $darkGray;
}
.diamond-button.is-background-red:hover {
    background-color: $darkRed;
}
.diamond-button.is-background-green:hover {
    background-color: $darkGreen;
}
.diamond-button.is-background-orange:hover {
    background-color: $darkOrange;
}
.diamond-button.is-background-blue:hover {
    background-color: $darkBlue;
}

.diamond-button.is-background-gray.is-light:hover {
    background-color: $gray;
    color: $lightGray;
}
.diamond-button.is-background-red.is-light:hover {
    background-color: $red;
    color: $lightRed;
}
.diamond-button.is-background-green.is-light:hover {
    background-color: $green;
    color: $lightGreen;
}
.diamond-button.is-background-orange.is-light:hover {
    background-color: $orange;
    color: $lightOrange;
}
.diamond-button.is-background-blue.is-light:hover {
    background-color: $blue;
    color: $lightBlue;
}

.diamond-button.is-background-white {
    color: $black;
}
.diamond-button.is-background-gray.is-light {
    color: $gray;
}
.diamond-button.is-background-red.is-light {
    color: $red;
}
.diamond-button.is-background-green.is-light {
    color: $green;
}
.diamond-button.is-background-orange.is-light {
    color: $orange;
}
.diamond-button.is-background-blue.is-light {
    color: $blue;
}

.diamond-button.diamond-button-disabled {
    pointer-events: none;
    background-color: $lightGray;
    color: $gray;
    border-color: $gray;
}

.diamond-field {
    border-color: $lightGray;
    border-style: solid;
    border-width: 1px;
    border-radius: 5px;
    padding-top: 4px;
    padding-bottom: 4px;
    font-family: inherit;
    display: block;
}

.diamond-field-addon {
    margin: 2px;
    padding: 2px;
    width: 1em;
    height: 1em;
    font-size: 10pt;
}

.diamond-field-addon-button {
    cursor: pointer;
    background-color: $lightGray;
    border: 1px solid $gray;
    margin: 0;
    padding: 2px;
    overflow: hidden;
}


.diamond-field:hover {
    outline: 1px solid $gray;
}

.diamond-field-select {
    background-color: inherit;
    cursor: pointer;
}

.diamond-field-select>select {
    background-color: inherit;
    cursor: pointer;
}

.diamond-field:focus-within {
    outline: 1px solid $gray;
    box-shadow: 0 0 0 0.1em $lightGray;
}

input, select {
    border: none;
    font-family: inherit;
}

input:focus, select:focus {
    outline: none;
}

.diamond-field.diamond-ok {
    border-color: $green;
    outline-color: $green;
}

.diamond-ok .diamond-field-addon-icon {
    color: $green;
}

.diamond-field.diamond-error {
    border-color: $red;
    outline-color: $red;
}

.diamond-error .diamond-field-addon-icon {
    color: $red;
}

.diamond-field.diamond-warning {
    border-color: $orange;
    outline-color: $orange;
}

.diamond-warning .diamond-field-addon-icon {
    color: $orange;
}

.diamond-field.diamond-info {
    border-color: $blue;
    outline-color: $blue;
}

.diamond-field.diamond-info .diamond-field-addon-icon {
    color: $blue;
}

.diamond-field-label {
    font-family: inherit;
    font-size: 0.8em;
    font-weight: bold;
    margin: 2px;
}

// components specific styles

.diamond-main {
    overflow: hidden;
    height: 100vh;
    max-height: 100vh;
    width: 100vw;
    max-width: 100vw;
    display: flex;
    flex-grow: 1;
}

// panel components

.diamond-layout-fit {
    display: flex !important;
    box-sizing: border-box;
    max-height: 100%;
    align-items: stretch;
}

.diamond-layout-hbox {
    display: flex !important;
    flex-direction: row;
    max-height: 100%;
    align-items: stretch;
}

.diamond-layout-vbox {
    display: flex !important;
    flex-direction: column;
    max-height: 100%;
    align-items: stretch;
}

.diamond-layout-card {
    display: flex !important;
    box-sizing: border-box;
    max-height: 100%;
    align-items: stretch;
}

.diamond-layout-fit>* {
    flex-grow: 1;
}

.diamond-layout-card>* {
    flex-grow: 0;
    display: none;
}

.diamond-layout-card>.is-active {
    flex-grow: 1;
    display: flex;
}

.diamond-panel-header {
    padding: 5px;
}
.diamond-panel-header>* {
    padding-right: 5px;
}

.diamond-panel-footer {
    padding: 5px;
}
.diamond-panel-footer>* {
    padding-right: 5px;
}

.diamond-collapsible-panel {
    border-color: $lightGray;
    border-style: solid;
    border-width: 1px;
}

.diamond-collapsible-panel-button{
    background-color: $lightGray;
    border-color: $lightGray;
    border-style: solid;
    border-left-width: 1px;
    padding-left: 1px;
    padding-right: 1px;
}

.diamond-collapsible-panel-button:hover{
    background-color: $lighterGray;
}

.diamond-boxed {
    box-shadow: 0 0.5em 1em -0.125em rgb(10 10 10 / 10%), 0 0px 0 1px rgb(10 10 10 / 2%);
    border-radius: 6px;
}

.diamond-dialog-header {
    padding: 10px;
    justify-content: space-between;
    border-style: solid;
    border-bottom-width: 1px;
    border-bottom-color: $lightGray;
}

.diamond-modal-dialog {
    background-color: rgba(248, 248, 248, 0.85);
    z-index: 99999;
}

// lazy table

.public_fixedDataTableRow_highlighted, .public_fixedDataTableRow_highlighted .public_fixedDataTableCell_main {
    background-color: $lighterBlue;;
}

.public_fixedDataTable_header, .public_fixedDataTable_scrollbarSpacer, .public_fixedDataTable_header .public_fixedDataTableCell_main {
    background-color: white;
    background-image: none;
}

.diamond-lazytable-selectable-row {
    cursor: default;
}

.diamond-lazytable-row-selected * {
    background-color: $blue !important;
    color: white;
}

.diamond-lazytable-row-warning * {
    background-color: rgb(255, 235, 235);
}

.diamond-lazytable-footer {
    background-color: $lighterGray;
}

// menu

.diamond-menu {
    margin: 5px;
}

.diamond-menu-item {
    padding: 7px;
    cursor: default;

}

.diamond-menu-item a {
    cursor: pointer;
    color: inherit;
    text-decoration: inherit;
}

.diamond-menu-item a:visited {
    cursor: pointer;
    color: inherit;
    text-decoration: inherit;
}

.diamond-menu-item:hover {
    background-color: $backgroundLight;
}

.diamond-menu-item.is-active {
    background-color: $activeItem;
    color: white;
}

.diamond-menu-group-caption {
    text-transform: uppercase;
    font-weight: bold;
    font-size: 0.9em;
    color: $gray;
    cursor: default;
    margin-top: 5px;
    margin-bottom: 5px;
}

// overlay

.diamond-lds-ring {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
}

.diamond-lds-ring div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 64px;
    height: 64px;
    margin: 8px;
    border: 8px solid rgb(158, 214, 221);
    border-radius: 50%;
    animation: diamond-lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: rgb(158, 214, 221) transparent transparent transparent;
}

.diamond-lds-ring div:nth-child(1) {
    animation-delay: -0.45s;
}

.diamond-lds-ring div:nth-child(2) {
    animation-delay: -0.3s;
}

.diamond-lds-ring div:nth-child(3) {
    animation-delay: -0.15s;
}

@keyframes diamond-lds-ring {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.diamond-overlay-loader-text {
    font-weight: bold;
    color: rgb(41, 41, 186);
}

// alert

.diamond-alert {
    display: flex;
    flex-direction: row;
    border-radius: 1rem;
    padding: 1rem;
    border-style: solid;
    border-width: 1px;
}

.diamond-alert>* {
    padding-left: 3px;
    padding-right: 3px;
}

.diamond-alert.diamond-info {
    background-color: $blue;
    color: $lightBlue;
}

.diamond-alert.diamond-info.is-light {
    background-color: $lightBlue;
    color: $blue;
}

.diamond-alert.diamond-ok {
    background-color: $green;
    color: $lightGreen;
}

.diamond-alert.diamond-ok.is-light {
    background-color: $lightGreen;
    color: $green;
}

.diamond-alert.diamond-warning {
    background-color: $orange;
    color: $lightOrange;
}

.diamond-alert.diamond-warning.is-light {
    background-color: $lightOrange;
    color: $orange;
}

.diamond-alert.diamond-error {
    background-color: $red;
    color: $lightRed;
}

.diamond-alert.diamond-error.is-light {
    background-color: $lightRed;
    color: $red;
}

.diamond-alert.diamond-verbose {
    background-color: $gray;
    color: $lightGray;
}

.diamond-alert.diamond-verbose.is-light {
    background-color: $lightGray;
    color: $gray;
}

// tabs

.diamond-tabs-tab {
    font-size: 1.2em;
    cursor: pointer;
    border-bottom: 2px solid $gray;
    display: flex;
    align-items: flex-end;
}

a.diamond-tabs-tab {
    text-decoration: none;
}
a.diamond-tabs-tab:visited {
    color: unset;
}

.diamond-tabs-tab>div {
    display: flex;
    align-items: flex-end;
}

.diamond-tabs-tab>div>div {
    padding-left: 10px;
    padding-right: 20px;
    margin-top: 10px;
    margin-bottom: 2px;
    user-select: none;
}

.diamond-tabs-tab>div>a {
    text-decoration: none;
    color: inherit;
}

.diamond-tabs-tab>div>i {
    margin-bottom: 3px;
    user-select: none;
}

.diamond-tabs-tab:hover {
    background-color: $lightGray;
}

.diamond-tabs-tab-active {
    border-bottom: 2px solid $black;
}

.diamond-tabs-tab.diamond-tabs-tab-boxed {
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    border: 1px solid $lightGray;
    border-bottom: 1px solid $blue;
}

.diamond-tabs-tab.diamond-tabs-tab-boxed>div {
    margin-left: 5px;
    margin-right: 5px;
    padding-left: 5px;
    padding-right: 5px;
}


.diamond-tabs-tab.diamond-tabs-tab-boxed.diamond-tabs-tab-active {
    background-color: $blue;
    color: white;
}

.diamond-tabs-tab.diamond-tabs-tab-disabled {
    pointer-events: none;
    background-color: $lighterGray;
    color: $gray;
}

.diamond-tabs-content {
    padding-top: 2px;
}

a {
    text-decoration: none;
    color: $blue;
}

a:visited {
    text-decoration: none;
}

.fixedDataTableCellGroupLayout_cellGroup > .public_fixedDataTableCell_main {
    white-space: nowrap;
}

.public_fixedDataTableCell_main {
    white-space: nowrap;
}